taxDeductibles.push({
  name: "Insurance Premiums and Interest on Savings (Versicherungsprämien und Zinsen von Sparkapitalien)",
  category: "Versicherungsbeiträge",
  description: "Deductions for insurance premiums and interest on savings, including additional deductions for children and supported persons.",
  isApplicable: (taxpayer) => taxpayer.healthInsurancePremiums > 0 || taxpayer.securitiesIncome > 0,
  calculateState: (taxpayer) => {
    let baseDeduction = taxpayer.maritalStatus === 'married' ? 5200 : 2600;
    if (!taxpayer.hasSecondPillar && !taxpayer.thirdPillarContributions) {
      baseDeduction *= 1.5;
    }
    const maxBaseDeduction = taxpayer.maritalStatus === 'married' ? 7800 : 3900;
    baseDeduction = Math.min(baseDeduction, maxBaseDeduction);

    const childDeduction = (taxpayer.numberOfChildren || 0) * 1300;
    const supportedPersonsDeduction = (taxpayer.supportedPersons || 0) * 1300;

    const totalDeduction = baseDeduction + childDeduction + supportedPersonsDeduction;
    return Math.min(taxpayer.healthInsurancePremiums + taxpayer.securitiesIncome, totalDeduction);
  },
  calculateFederal: (taxpayer) => {
    let baseDeduction = taxpayer.maritalStatus === 'married' ? 3500 : 1700;
    if (!taxpayer.hasSecondPillar && !taxpayer.thirdPillarContributions) {
      baseDeduction *= 1.5;
    }
    const maxBaseDeduction = taxpayer.maritalStatus === 'married' ? 5250 : 2550;
    baseDeduction = Math.min(baseDeduction, maxBaseDeduction);

    const childDeduction = (taxpayer.numberOfChildren || 0) * 700;
    const supportedPersonsDeduction = (taxpayer.supportedPersons || 0) * 700;

    const totalDeduction = baseDeduction + childDeduction + supportedPersonsDeduction;
    return Math.min(taxpayer.healthInsurancePremiums + taxpayer.securitiesIncome, totalDeduction);
  },
  requiredAttributes: [
    "healthInsurancePremiums",
    "securitiesIncome",
    "maritalStatus",
    "hasSecondPillar",
    "thirdPillarContributions",
    "numberOfChildren",
    "supportedPersons"
  ],
  calculationBasis: 'per household',
  notes: "The deduction is calculated based on marital status, presence of 2nd and 3rd pillar contributions, number of children, and supported persons. The actual deduction is limited to the total of paid insurance premiums and interest on savings."
});

